@keyframes jump {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.02);
  }
}
.jumping {
  animation: 0.4s jump ease infinite alternate;
}

.lottie-library {
  margin: 0 auto;
  max-width: 50em;
}

.lottie-input-cursor {
  position: absolute;
  z-index: 1;
  transform: translateY(-10px);
  pointer-events: none;
}

@media (max-width: 1150px) {
  .lottie-library {
    width: 90vw !important;
  }
  .lottie-input-hand {
    position: absolute;
    z-index: 1;
    pointer-events: none;
  }
}